import React, { Component } from 'react';
import {Container,Row, Col} from 'reactstrap';
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import SectionHeader from '../SectionHeader';

class Products extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount(){
        let bodyWidth=document.body.clientWidth;
        if(document.getElementsByClassName('custom-container').length){
            let containerWidth=document.getElementsByClassName('custom-container')[0].clientWidth;
            let remainingWidth=bodyWidth-containerWidth;
            let singleWidth=remainingWidth/2;
            this.refs.padding.setAttribute('style',`padding-right:${singleWidth}px`)
        }
      
    }

    render() {
        var settings = {
            dots: false,
            infinite: true,
            autoplay: true,
            autoplaySpeed: 2000,
            slidesToShow: 2,
            slidesToScroll: 1,
            touchThreshold: 100
          };
        return (
            <div className="section section-products">
                <Container className="custom-container"></Container>
                <Row className="align-items-center">
                    <Col lg="6" >
                        <div className="section-left">
                            <div className="product-wrapper">
                                <Slider {...settings}>
                                    <div className="product-item">
                                        <div className="image">
                                            <img src={require('../../images/products/product-1.png')} />
                                        </div>
                                    </div>
                                    <div className="product-item">
                                        <div className="image">
                                            <img src={require('../../images/products/product-2.png')} />
                                        </div>
                                    </div>
                                    <div className="product-item">
                                        <div className="image">
                                            <img src={require('../../images/products/product-1.png')} />
                                        </div>
                                    </div>
                                </Slider>
                            </div>
                        </div>
                    </Col>
                    <Col lg='6'>
                        <div className="section-right" ref="padding">
                             
                             <SectionHeader title="See our product more..."/>
                             <div className="section-description">
                                is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. 
                             </div>
                        </div>
                    </Col>
                </Row>
                <img src={require('../../images/triangle-6.png')} className="triangle-6"/>
            </div>
        );
    }
}


export default Products;
