import React, { Component, Fragment } from 'react';
import {
    Container,
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    Button
   } from 'reactstrap';

import logo from '../../images/Armada_logo_horiz_white.png';
const menuItem=[
    {/*
    {
        menu:'Products',
        url:'/products'
    },
    {
        menu:'Company',
        url:'/company'
    },
    {
        menu:'Blog',
        url:'/blog'
    },
    {
        menu:'Career',
        url:'/career'
    },
    {
        menu:'Sign in',
        url:'/sign-in'
    },
    */},
    {
        menu:'Beta Signup',
        url:'https://armadachain.typeform.com/to/tgBbv5',
        type:'button'
    }
];
export default class Header extends Component{
    constructor(props) {
        super(props);
    
        this.toggle = this.toggle.bind(this);
        this.state = {
          isOpen: false,

        };
      }
      toggle() {
        this.setState({
          isOpen: !this.state.isOpen
        });
      }
      
    render(){
        const Navigation=menuItem.map((item,index)=>{
            return (
                <NavItem key={index}>
                    <NavLink href={item.url} className={item.type?item.type:''}>{item.menu}</NavLink>
                </NavItem>
            );
        })
        return(
            <Fragment>
                <Navbar  expand="lg">
                    <Container>
                        <NavbarBrand href="/"><img src={logo}/></NavbarBrand>
                        <NavbarToggler onClick={this.toggle} />
                        <Collapse isOpen={this.state.isOpen} navbar>
                            <Nav className="ml-auto" navbar>
                                {Navigation}
                            
                            </Nav>
                        </Collapse>
                    </Container>
                </Navbar>
            </Fragment>
        );
    }
}